<template>
  <div>
    <!-- 卡片容器 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>账号列表</span>
      </div>
      <!-- 账号列表表格 -->
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="账号" width="120">
          <template slot-scope="scope">{{ scope.row.account }}</template>
        </el-table-column>
        <el-table-column prop="userGroup" label="用户组" width="120"></el-table-column>
        <el-table-column prop="ctime" label="创建时间" show-overflow-tooltip></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="clickEdit(scope.$index, scope.row)">编辑</el-button>
            <!-- 编辑-模态框 -->
            <el-dialog title="修改账号" :visible.sync="dialogFormVisible">
              <el-form :model="form">
                <el-form-item label="变更账号" :label-width="formLabelWidth">
                  <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户组" :label-width="formLabelWidth">
                  <el-select v-model="form.region" placeholder="修改用户组">
                    <el-option label="超级管理员" value="超级管理员"></el-option>
                    <el-option label="普通管理员" value="普通管理员"></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancelEdit">取 消</el-button>
                <el-button type="primary" @click="accEditSave">确 定</el-button>
              </div>
            </el-dialog>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div id="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 50]"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
      <!-- 批量操作 -->
      <div id="listBtn">
        <el-button @click="delchecked" class="delChecked" type="danger">批量删除</el-button>
        <el-button class="unChecked" type="primary">取消选择</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
// 引入接口
import { API_GET_USERLIST } from "@/API/apis.js";
import { API_DELETE_ACC } from "@/API/apis.js";
import { API_DELETE_CHECKED } from "@/API/apis.js";
import { API_EDIT_ACCOUNT } from "@/API/apis.js";
export default {
  data() {
    return {
      // 分页-当前激活页数
      currentPage: 1,
      //总页数
      total: 0,
      //每页显示条数
      pageSize: 5,
      // 账号列表表格数据
      tableData: [],
      multipleSelection: [],

      // 模态框
      dialogFormVisible: false,
      //模态框内的form
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px",
      // 编辑项的ID
      editId: null
    };
  },
  methods: {
    // 编辑账号（弹出模态框）
    clickEdit(index, row) {
      //弹出模态框，并获取id
      this.dialogFormVisible = true;
      //console.log(row.id);
      this.editId = row.id;
    },
    //保存修改
    accEditSave() {
      // 获取新的用户信息 id acc usergroup
      //  this.editId  this.form.name  this.form.region
      API_EDIT_ACCOUNT(this.editId, this.form.name, this.form.region).then(
        res => {
          if (res) this.getuserlist(); //重新获取列表
          this.form.name = ""; //清空输入框
          this.form.region = "";
        }
      );
      this.dialogFormVisible = false; //隐藏模态框
      this.$message({
        message: "变更账号信息成功",
        type: "success",
        duration: 1000
      });
    },
    // 取消修改
    cancelEdit() {
      this.dialogFormVisible = false;
      this.form.name = ""; //清空输入框
      this.form.region = "";
    },
    // 删除账号
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          API_DELETE_ACC(row.id).then(res => {
            // console.log(res.data.code)
            if (res.data.code == 0) this.getuserlist();
          });
          this.$message({
            type: "success",
            message: "删除成功!",
            duration: 1500
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
            duration: 1500
          });
        });
        this.currentPage=1;
    },
    // 批量删除
    delchecked() {
      let arr = [];
      let ids = [];
      for (let item of this.multipleSelection) {
        arr.push(item.id);
        ids = JSON.stringify(arr);
      }
      this.$confirm("永久删除已选用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          API_DELETE_CHECKED(ids).then(res => {
            if (res.data.code == 0) this.getuserlist();
          });
          this.$message({
            type: "success",
            message: "删除成功!",
            duration: 1500
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
            duration: 1500
          });
        });
        this.currentPage=1;
    },
    // 改变每页显示条数
    handleSizeChange(val) {
      this.pageSize = val;
      this.getuserlist();
    },
    // 改变当前页
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getuserlist();
    },
    // 封装的获取账户列表方法
    getuserlist() {
      API_GET_USERLIST(this.currentPage, this.pageSize).then(res => {
        if (res.statusText == "OK") {
          //如果请求成功
          //res.data.data.ctime=res.data.data.ctime.slice(1,10)
          this.total = res.data.total; // 获取数据条数
          this.tableData = res.data.data; //获取表格数据
          // 修改时间格式
          for (let i of this.tableData) {
            i.ctime = i.ctime.slice(0, 10); //截取时间（只保留年月日）
          }
        }
      });
    },
    // 表格方法
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  },
  // Vue生命周期created阶段：组件,data,事件已初始化
  created() {
    //console.log(this.currentPage,this.pageSize);
    this.getuserlist(); //重新获取用户列表
  }
};
</script>

<style lang="less" scoped>
#pagination {
  margin-top: 15px;
}
#listBtn {
  margin-top: 15px;
}
</style>